import {Recipe} from '../model/Recipe';
import {Plan} from '../model/Plan';
import {ScoringFun} from '../model/ScoringFun';
import {Scoring} from '../model/Scoring';

import {RecipeIndex} from '../db/RecipeDB';

import {Parser} from 'expr-eval';

import _ from 'lodash';

const scoringFuns = [
    new ScoringFun("random", "random(1)"),
    new ScoringFun("SLOW", "planHasLabel(\"SLOW\") > 0 and hasLabel(this, \"SLOW\") ? -1 : 0"),
    new ScoringFun("+1", "hasLabel(this, \"+1\") ? 0.15 : 0"),
    new ScoringFun("-1", "hasLabel(this, \"-1\") ? -0.15 : 0"),
    new ScoringFun("EASY", "hasLabel(this, \"EASY\") ? 0.15/(1+planHasLabel(\"EASY\")) : 0"),
    new ScoringFun("Side dish", "hasLabel(this, \"SD\") ? (planHasLabel(\"SD\") < planHasLabel(\"NSD\") ? 5 : -5) : 0"),
    new ScoringFun("Few ingredients", "(1-normalize(length(ingredients(this))))/6"),
    new ScoringFun("Repetition", "planHasRecipe(this) > 0 and (not hasLabel(this, \"SD\")) ? -2 : 0")
]

function labelDict(r: Recipe)
{
    return _.countBy(r.getLabels().map(l => l.toUpperCase()));
}

function _scoreRecipes(p: Parser, recipes: Recipe[], fun: ScoringFun)
{
    return new Map(recipes.map(r=> {
        p.consts.this = r;
        let s = p.evaluate(fun.getScript());
        return [r, s]
    }));
}

export function scoreRecipes(recipeIndex: RecipeIndex, plan: Plan)
{
    let p = new Parser();

    let recipes = recipeIndex.all();
    let rlabels = new Map(recipes.map(r => [r, labelDict(r)]))
    let precipes = _.countBy(recipeIndex.getAll(plan.getAllRecipeIds(false)).map(r => r.getId()))
    let plabels = _.countBy(_.flatMap(recipeIndex.getAll(plan.getAllRecipeIds(false)), r=>r.getLabels()).map(l=>l.toUpperCase()))

    p.functions.intersection = (as: any[], bs: any[]) => {
        return _.intersection(as, bs);
    }

    p.functions.sum = (as: number[]) => {
        return _.sum(as);
    }

    p.functions.ingredients = (r: Recipe) => {
        return r.getIngredients();
    }

    p.functions.hasLabel = (r: Recipe, label: string) => {
        let ld = rlabels.get(r);
        if(ld)
        {
            return ld[label] > 0;
        }
        else
        {
            return false;
        }
    }

    p.functions.planHasLabel = (label: string) => {
        return plabels[label] ?? 0;
    }

    p.functions.planHasRecipe = (r: Recipe) => {
        return precipes[r.getId()] ?? 0;
    }

    let scoring = new Scoring()

    for(let sf of scoringFuns)
    {
        let max = -1;

        p.functions.normalize = (curr: number) =>
        {
            if(curr > max)
            {
                max = curr;
            }
        
            return curr;
        }

        let scores = _scoreRecipes(p, recipes, sf);

        // If `normalize` was used, we need to run it again
        if(max !== -1)
        {
            p.functions.normalize = (curr: number) =>
            {
                return curr / max;
            }
    
            scores = _scoreRecipes(p, recipes, sf);
        }

        for(let [r, s] of scores)
        {
            scoring.addScoring(r.getId(), sf, s);
        }
    }

    return scoring;
}
